@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modern-home {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f5f0eb;
  height: 100vh;
  font-family: 'Cormorant Garamond', serif;
  padding: 40px;
  box-sizing: border-box;
}

.left-section {
  max-width: 40%;
  font-size: 18px;
  line-height: 1.6;
  color: #222;
}

.left-section h4 {
  font-size: 20px;
  letter-spacing: 2px;
  margin-bottom: 20px;
  font-weight: bold;
}

.right-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-size: 60px;
  grid-gap: 20px;
  gap: 20px;
}

.right-section a {
  text-decoration: none;
  color: #111;
  font-weight: bold;
  transition: all 0.3s ease;
}

.right-section a:hover {
  color: #999;
  letter-spacing: 2px;
}

.about-container {
  display: flex;
  flex-wrap: wrap;
  background-color: #f5f0eb;  
  min-height: 100vh;
  font-family: 'Cormorant Garamond', serif;
  padding: 60px 40px 40px 40px;
  box-sizing: border-box;
  align-items: flex-start;
}

.about-left {
  width: 100%;
  max-width: 25%;
  padding-right: 30px;
  color: #222;
  text-align: center;
}

.about-left h4 {
  font-size: 20px;
  letter-spacing: 2px;
  margin-bottom: 10px;
  font-weight: bold;
}

.about-left p {
  font-size: 50px;
  font-weight: bold;
  margin: 0;
}

.about-right {
  width: 100%;
  max-width: 70%;
  font-size: 18px;
  line-height: 1.8;
  color: #333;
}

.projects-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: #f5f0eb; /* same as Home/About */
  min-height: 100vh;
  font-family: 'Cormorant Garamond', serif;
  padding: 60px 40px;
  box-sizing: border-box;
}
.projects-left {
  width: 25%;
  color: #222;
  text-align: center;
}

.projects-left h4 {
  font-size: 20px;
  letter-spacing: 2px;
  margin-bottom: 10px;
  font-weight: bold;
}

.projects-left p {
  font-size: 48px;
  font-weight: bold;
  margin: 0;
}

.projects-right {
  width: 70%;
  padding-left: 30px;
  color: #333;
}

h2 {
  font-size: 28px;
  margin-bottom: 20px;
  border-bottom: 2px solid #ccc;
  padding-bottom: 8px;
}

.experience-entry {
  margin-bottom: 24px;
  border-bottom: 1px dashed #bbb;
  padding-bottom: 16px;
}

.experience-entry h3 {
  font-size: 20px;
  cursor: pointer;
  color: #2c2c2c;
  margin: 0;
  transition: color 0.3s ease;
}

.experience-entry h3:hover {
  color: #0077cc;
}

.duration {
  font-weight: normal;
  font-size: 14px;
  color: #777;
  margin-left: 10px;
}

.experience-entry ul {
  margin-top: 10px;
  padding-left: 20px;
  line-height: 1.6;
}

.project {
  margin-bottom: 30px;
}

.project h3 {
  margin: 0 0 5px;
  font-size: 20px;
}

.project a {
  display: inline-block;
  margin-top: 6px;
  color: #0077cc;
  text-decoration: none;
  font-weight: bold;
}

.project a:hover {
  text-decoration: underline;
}

.contact-container {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  background-color: #eaf4f4; /* new elegant teal tone */
  min-height: 100vh;
  font-family: 'Cormorant Garamond', serif;
  padding: 40px;
  box-sizing: border-box;
}

.contact-left {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: #222;
}

.contact-left h4 {
  font-size: 20px;
  letter-spacing: 2px;
  margin-bottom: 10px;
  font-weight: bold;
}

.contact-left p {
  font-size: 48px;
  font-weight: bold;
  margin: 0;
}

.contact-right {
  width: 70%;
  font-size: 18px;
  line-height: 1.8;
  color: #333;
  display: flex;
  flex-direction: column;
  grid-gap: 25px;
  gap: 25px;
  padding-left: 10px;  /* Shift content closer to left */
  align-items: flex-start;
}

.contact-item {
  display: flex;
  align-items: center;
  grid-gap: 12px;
  gap: 12px;
}

.contact-icon {
  font-size: 22px;
  color: #444;
}

